@font-face {
  font-family: "thisco_Brown";
  src: url("https://www.thiscovery.org/wp-content/themes/thiscovery/fonts/Brown-Regular.woff2") format("woff2"), url("https://www.thiscovery.org/wp-content/themes/thiscovery/fonts/Brown-Regular.woff") format("woff"), url("https://www.thiscovery.org/wp-content/themes/thiscovery/fonts/Brown-Regular.otf") format("opentype");
  font-style: normal;
  font-weight: 300 600;
}
@font-face {
  font-family: "thisco_Brown";
  src: url("https://www.thiscovery.org/wp-content/themes/thiscovery/fonts/Brown-Bold.woff2") format("woff2"), url("https://www.thiscovery.org/wp-content/themes/thiscovery/fonts/Brown-Bold.woff") format("woff"), url("https://www.thiscovery.org/wp-content/themes/thiscovery/fonts/Brown-Bold.otf") format("opentype");
  font-style: normal;
  font-weight: 700 900;
}
@font-face {
  font-family: "thisco_Brown";
  src: url("https://www.thiscovery.org/wp-content/themes/thiscovery/fonts/Brown-Light.woff2") format("woff2"), url("https://www.thiscovery.org/wp-content/themes/thiscovery/fonts/Brown-Light.woff") format("woff"), url("https://www.thiscovery.org/wp-content/themes/thiscovery/fonts/Brown-Light.otf") format("opentype");
  font-style: normal;
  font-weight: 100 200;
}
/* define official THIS colours */
:root {
  --thisco-dark-grey: rgb(58 60 66 / 1);
  --thisco-light-grey: rgb(210 212 211 / 1);
  --thisco-red: rgb(221, 0, 49);
  --thisco-bright-blue: rgb(91, 194, 231);
  --thisco-pale-blue: rgb(143, 172, 217);
  --thisco-green: rgb(0, 187, 126);
  --thisco-pink: rgb(243, 165, 185);
  --thisco-aubergine: rgb(104 4 54 / 1);
  --thisco-yellow: rgb(255 216 90 / 1);
}

/* 

mobile-first layout ?? might be fighting against Qualtrics breakpoints....  
Q breakpoints seem to be... [ <480px, <767px (some weird 770px ones?), <979px ]

2.1.0 - ejects Q stylesheet

*/
html {
  font-size: 14px;
}

body {
  min-height: 100vh;
  background-color: var(--thisco-light-grey);
}

.Skin {
  font-family: "thisco_Brown", "Brown-Regular", Arial, "Helvetica Neue", Helvetica, sans-serif;
  color: rgba(0, 0, 0, 0.95);
  font-weight: 100;
  font-size: 18px;
  line-height: 1.4;
  background-color: white;
  /* basic resets */
  /* end resets */
}
@media (min-width: 481px) {
  .Skin {
    font-size: 18px;
  }
}
@media (min-width: 768px) {
  .Skin {
    font-size: 20px;
  }
}
.Skin h1, .Skin h2, .Skin h3, .Skin h4 {
  line-height: 1.2;
  margin-top: 1em;
  margin-bottom: 1em;
}
.Skin a, .Skin a:hover, .Skin a:visited, .Skin a:focus {
  color: inherit;
  text-decoration: none;
}
.Skin .SkinInner {
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .Skin .SkinInner {
    padding-top: 6rem;
  }
}
.Skin #HeaderContainer {
  background-color: var(--thisco-light-grey);
  position: fixed;
  left: 0px;
  width: 100vw;
  z-index: 100;
  top: 0px;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.1);
  padding: 0px 1rem;
}
