@media (min-width: 768px) {
  .Skin #HeaderContainer #Header {
    height: 6rem;
    background-position: 1.6rem 50%;
  }
}
@media (min-width: 1400px) {
  .Skin #HeaderContainer #Header {
    background-position: calc(50vw - 700px) 50%;
  }
}
.Skin #HeaderContainer #Header .header-content {
  height: 100%;
  width: 100%;
  max-width: 1400px;
  display: grid;
  grid-column-templates: 1fr 1fr 1fr;
  align-items: center;
}
@media (min-width: 1400px) {
  .Skin #HeaderContainer #Header .header-content {
    margin-left: calc(50vw - 700px);
  }
}
.Skin #HeaderContainer #Header .header-content .back-button {
  grid-column-start: 3;
  max-width: 24ch;
  font-size: 0.7em;
  padding: 10px 12px;
  text-align: right;
  justify-self: flex-end;
  margin-right: 0rem;
  font-weight: 400;
}
@media (min-width: 980px) {
  .Skin #HeaderContainer #Header .header-content .back-button {
    margin-right: 2rem;
  }
}
.Skin .QuestionText, .Skin .DB .QuestionText {
  color: inherit;
  font-weight: inherit;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
.Skin .QuestionOuter {
  padding: 0;
}
.Skin #Footer {
  background-color: var(--thisco-light-grey);
  position: absolute;
  width: 100%;
  max-width: initial;
  margin-top: 0px;
  padding: 5rem 1rem;
  padding-bottom: 1rem;
  width: 100vw;
  font-size: 14px;
}
.Skin #Footer .footer-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1em;
  align-items: flex-end;
  width: clamp(80vw, 100%, 1400px);
}
